import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { MdAddBox } from 'react-icons/md'
import { TiDeleteOutline } from 'react-icons/ti'
import { devUrl } from '../config'
import FukuanActivityPage from '../pages/cart/FukuanActivityPage'
import Buttonx3 from '../components/Cart/Buttonx3'
import ConfirmPuzzle from '../components/Cart/ConfirmPuzzle'
import ConfirmWave from '../components/Cart/ConfirmWave'

function Cart() {
  const [mycart, setMycart] = useState([])
  const [dataLoading, setDataLoading] = useState(false)
  const [mycartDisplay, setMycartDisplay] = useState([])

  function getCartFromLocalStorage() {
    // 
    setDataLoading(true)

    const newCart = localStorage.getItem('cart') || '[]'

    console.log(JSON.parse(newCart))

    setMycart(JSON.parse(newCart))
  }

  useEffect(() => {
    getCartFromLocalStorage()
  }, [])

  // mycart1
  // componentDidUpdate
  useEffect(() => {
    setTimeout(() => setDataLoading(false), 1000)

    // mycartDisplay
    let newMycartDisplay = []

    //mycartDisplay
    for (let i = 0; i < mycart.length; i++) {
      //mycartDisplaymycart[i].id
      //
      //-1
      const index = newMycartDisplay.findIndex(
        (value) => value.id === mycart[i].id
      )
      //+1
      if (index !== -1) {
        //1
        //newMycartDisplay[index].amount++
        //
        newMycartDisplay[index].amount += mycart[i].amount
      } else {
        //1
        const newItem = { ...mycart[i] }
        newMycartDisplay = [...newMycartDisplay, newItem]
      }
    }

    console.log(newMycartDisplay)
    setMycartDisplay(newMycartDisplay)
  }, [mycart])

  // 
  const updateCartToLocalStorage = (item, isAdded = true) => {
    console.log(item, isAdded)
    const currentCart = JSON.parse(localStorage.getItem('cart')) || []

    // find if the product in the localstorage with its id
    const index = currentCart.findIndex((v) => v.id === item.id)

    console.log('index', index)
    // found: index! == -1
    if (index > -1) {
      isAdded ? currentCart[index].amount++ : currentCart[index].amount--
    }

    localStorage.setItem('cart', JSON.stringify(currentCart))

    // 
    setMycart(currentCart)
  }

  // 
  const sum = (items) => {
    let total = 0
    for (let i = 0; i < items.length; i++) {
      total += items[i].amount * items[i].price
    }
    return total
  }

  const loading = (
    <>
      <div
        className="d-flex justify-content-center align-items-center"
        style={{ minHeight: '100vh' }}
      >
        <div className="spinner-border" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    </>
  )

  const display = (
    <>
      <div className="shoppingdetailstopphoto">
        <img src={devUrl + '/pic/pic/Group 597.png'} alt="..." />
      </div>
      <Buttonx3 />
      <ConfirmPuzzle />
      <div className="activityordercolumnobb2">
        <div className="activityordercolumno">
          <table responsive className="columnoh">
            <thead className="centertrtd">
              <tr className="trco">
                <th className="checkboxaaa">
                  <input type="checkbox" />
                </th>
                <th className="imgw"></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            {/* <div className="lineup"></div> */}

            {mycartDisplay.length > 0 ? (
              mycartDisplay.map((item, index) => {
                return (
                  <tbody className="centertrtd">
                    <tr className="">
                      <td>
                        <input type="checkbox" />
                      </td>
                      <td className="tdcolumn d-flex">
                        <div>
                          <img
                            className="titlephoto"
                            src={devUrl + `/pic/event/${item.photoimg}`}
                            alt="titlephoto"
                          />
                        </div>{' '}
                        <div className="context">{item.name}</div>
                      </td>
                      <td className="">1</td>
                      <td>{item.amount * item.price}</td>
                      <td className="">
                        <Link to="#">
                          <MdAddBox className="iconcolor" />
                        </Link>
                      </td>
                      <td>
                        <Link to="#">
                          <TiDeleteOutline className="iconcolor2" />
                        </Link>
                      </td>
                    </tr>
                    <div className="lineup"></div>
                  </tbody>
                )
              })
            ) : (
              <></>
            )}
          </table>
        </div>
      </div>
      <div>
        <div className="detailsamountbox d-flex">
          <div className="boxx2 d-flex">
            <div className="d-flex voucher">
              <div className="lineh">
                <input type="checkbox" />
              </div>
              <div className="lineh"></div>
              <div className="inputbox d-flex">
                <input type="text" className="form-control form-control-md" />
              </div>
            </div>
          </div>
          <div className="boxx3 d-flex">
            <div>
              <div className="money d-flex">2</div>
              <div className="money d-flex"></div>
              <div className="money d-flex"></div>
            </div>
            <div>
              <div className="money d-flex moneyex">{sum(mycartDisplay)}</div>
              <div className="money d-flex moneyred"> - 0</div>
              <div className="money d-flex">{sum(mycartDisplay)}</div>
            </div>
          </div>
        </div>
      </div>
      <div className="fukuanbutton">
        <div className="b1 d-flex">
          <button className="btn d-flex confirmbutton1"></button>
          <Link
            to="/PaymentMethodActivityPage"
            className="btn d-flex confirmbutton2"
          >
            
          </Link>
        </div>
        <ConfirmWave />
      </div>
    </>
  )

  // 
  return dataLoading ? loading : display
}

export default Cart
