const yup = require('yup')

const Validationaaa = yup.object().shape({
  nowpass: yup.string().required(''),
  password: yup
    .string()
    .min(6, '6-12')
    .max(12, '6-12')
    .required(''),
  passwordConfirmation: yup
    .string()
    .oneOf([yup.ref('password'), null], '')
    .required(''),
})

export default Validationaaa
