import React, { useState, useEffect } from 'react'
import { Navbar, Nav, NavDropdown } from 'react-bootstrap'
import { MdSearch } from 'react-icons/md'
import { MdShoppingCart } from 'react-icons/md'
import { devUrl } from '../../config'
import '../../style/navbar.scss'

// 
import '../../style/soya/login2.scss'
import Axios from 'axios'
import Logout from '../Soya/Logout'
import { Modal, Button } from 'react-bootstrap'

import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch,
  Redirect,
  NavLink,
  withRouter,
} from 'react-router-dom'

function MyNavbar(props) {
  // 
  const { isAuth, setIsAuth } = props

  // id
  const [member, setMember] = useState()
  const [memberidd, setMemberidd] = useState('')
  const [memberInfo, setMemberInfo] = useState()

  //
  const [show, setShow] = useState(false)
  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)

  Axios.defaults.withCredentials = true

  // +session id
  useEffect(() => {
    Axios.get('http://localhost:3001/login')
      .then((response) => {
        // console.log(response)
        if (response.data.loggedIn === true) {
          // console.log(response.data)
          setMemberidd(response.data.user[0].member_id)
          setIsAuth(true)
        }
      })
      .then(() => {
        window.sessionStorage.setItem('useriddd', `${memberidd}`)
        const loginid = window.sessionStorage.getItem('useriddd')
        // console.log(loginid)
      })
  }, [isAuth])

  //
  useEffect(() => {
    if (memberidd !== '') {
      Axios.get(`http://localhost:3001/member/get/${memberidd}`).then(
        (response) => {
          setMemberInfo(response.data[0])
          console.log(response.data)
        }
      )
    }
  }, [memberidd])

  return (
    <>
      {/* .navbar-expand-{sm|md|lg|xl} */}
      <div className="navbar1">
        <Navbar
          collapseOnSelect
          expand="lg"
          bg="white"
          variant="light"
          fixed="top"
          style={{ padding: '0', height: '80px' }}
        >
          <Navbar.Brand as={NavLink} to="/" exact>
            <img
              className="Logo"
              src={devUrl + '/Pic/SVG/logo.svg'}
              alt=""
            />
          </Navbar.Brand>
          <Nav className="nav1">
            {props.isAuth ? (
              <Nav.Link className="navLink" as={NavLink} to="/eventstart">
                
              </Nav.Link>
            ) : (
              <button
                className="navLink btn btnstartjoinnn"
                as={NavLink}
                onClick={handleShow}
              >
                
              </button>
            )}
            <Modal
              show={show}
              onHide={handleClose}
              backdrop="static"
              keyboard={false}
            >
              <Modal.Header closeButton>
                <Modal.Title></Modal.Title>
              </Modal.Header>
              <Modal.Body></Modal.Body>
              <Modal.Footer>
                <Button
                  style={{ color: 'white' }}
                  variant="secondary"
                  onClick={handleClose}
                >
                  
                </Button>
                <Link to="/login">
                  <Button variant="primary" onClick={handleClose}>
                    
                  </Button>
                </Link>
              </Modal.Footer>
            </Modal>
            <Nav.Link className="navLink" as={NavLink} to="/event" exact>
              
            </Nav.Link>
            <Nav.Link className="navLink" as={NavLink} to="/class">
              
            </Nav.Link>
          </Nav>
          <Nav className="nav2">
            <Nav.Link className="icon" as={NavLink} to="/cart">
              <MdShoppingCart size={20} />
            </Nav.Link>
          </Nav>

          {isAuth ? (
            <div className="position-relative" style={{ marginRight: '75px' }}>
              <NavDropdown
                title=""
                id="basic-nav-dropdown"
                className="text-align-center"
              >
                <NavDropdown.Item
                  style={{
                    color: '#104b6d',
                    paddingBottom: '12px',
                    borderBottom: '1px solid rgba(0,0,0,0.2)',
                  }}
                >
                  !&nbsp;&nbsp;{memberInfo && memberInfo.member_name}
                </NavDropdown.Item>
                <NavDropdown.Item
                  href={`/member/${memberidd}`}
                  style={{ paddingTop: '12px' }}
                >
                  
                </NavDropdown.Item>
                <NavDropdown.Item href={`/member/${memberidd}/Myjoinin`}>
                  
                </NavDropdown.Item>
                <NavDropdown.Item href={`/member/${memberidd}/MyCollection`}>
                  
                </NavDropdown.Item>
                <NavDropdown.Item
                  href={`/member/${memberidd}/MyPhoto`}
                  style={{ paddingBottom: '12px' }}
                >
                  
                </NavDropdown.Item>
                <NavDropdown.Item
                  style={{
                    color: '#104b6d',
                    paddingTop: '12px',
                    borderTop: '1px solid rgba(0,0,0,0.2)',
                  }}
                >
                  <Logout isAuth={isAuth} setIsAuth={setIsAuth} />
                </NavDropdown.Item>
              </NavDropdown>
              <figure className="memberPhoto position-absolute">
                {memberInfo && (
                  <img
                    src={`${devUrl}/Pic/mem_img/${memberInfo.member_img}`}
                    alt=""
                  />
                )}
                {/* <img src={devUrl + '/Pic/pic/member.jpg'} alt="" /> */}
              </figure>
            </div>
          ) : (
            <button
              className="btn loginoutbtn"
              onClick={(e) => {
                props.history.push('/login')
              }}
            >
              
            </button>
          )}

          {/* </Navbar.Collapse> */}
        </Navbar>
      </div>
    </>
  )
}

export default withRouter(MyNavbar)
