import React, { useState, useEffect, Component } from 'react'
// import { Form, FormControl, Button, Row, Col, Container } from 'react-bootstrap'
import { devUrl } from '../../config'
import { Link } from 'react-router-dom'
import '../../style/cart/nexttimecolumn.scss'
import { IoIosCart } from 'react-icons/io'
import { TiDeleteOutline } from 'react-icons/ti'
import Todo from './Todo'

class NextTimeColumn extends Component {
  //  class 
  constructor(props) {
    super(props)

    // 
    this.onClick = this.onClick.bind(this)
    this.onChange = this.onChange.bind(this)
    this.removeTodo = this.removeTodo.bind(this)

    //  state
    this.state = {
      todos: [
        {
          id: 1,
          name: '~',
          amount: 1,
          price: 500,
          photoimg: '3.jpg',
        },
        {
          id: 2,
          name: '',
          amount: 1,
          price: 500,
          photoimg: '.jpg',
        },
        {
          id: 3,
          name: '',
          amount: 1,
          price: 1000,
          photoimg: '.jpeg',
        },
      ],
    }
  }

  // input  value
  onChange(e) {
    this.setState({
      text: e.target.value,
    })
  }

  onClick() {
    const { todos, text } = this.state
    const newId = todos[todos.length - 1].id + 1

    //  state
    this.setState({
      text: '',
      todos: [
        ...todos,
        { id: newId, name: text, amount: text, price: text, photoimg: text },
      ],
    })
  }

  removeTodo(id) {
    const { todos } = this.state

    //  filter 
    let newTodos = todos.filter((item) => item.id !== id)

    this.setState({
      todos: newTodos, //  todos: newTodos ?
    })
  }

  render() {
    //  state 
    const { todos, text } = this.state

    return (
      <div>
        <div className="nextactivityordercolumnobb">
          <div className="activityordercolumno">
            <table responsive className="columnoh">
              <thead className="centertrtd">
                <tr className="trco">
                  <th className="checkboxaaa">
                    <input type="checkbox" />
                  </th>
                  <th className="imgw"></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <div className="lineup"></div>

              <tbody className="centertrtd">
                {todos.map((todo) => (
                  <Todo
                    id={todo.id}
                    name={todo.name}
                    amount={todo.amount}
                    price={todo.price}
                    photoimg={todo.photoimg}
                    remove={this.removeTodo}
                  />
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    )
  }
}

export default NextTimeColumn
